export default ".hat-container {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  height: 200px;\r\n}\r\n\r\n.hat {\r\n  width: 100px;\r\n  height: 70px;\r\n  position: relative;\r\n  animation: bounceHat 1s ease-in-out infinite;\r\n}\r\n\r\n.hat-top {\r\n  width: 100px;\r\n  height: 50px;\r\n  background-color: #8b4513; /* SaddleBrown color for the hat */\r\n  border-radius: 50% 50% 0 0; /* Rounded top for hat */\r\n  position: absolute;\r\n  top: 0;\r\n}\r\n\r\n.hat-brim {\r\n  width: 120px;\r\n  height: 20px;\r\n  background-color: #8b4513; /* SaddleBrown color for the brim */\r\n  position: absolute;\r\n  bottom: 0;\r\n  left: 50%;\r\n  transform: translateX(-50%);\r\n}\r\n\r\n@keyframes bounceHat {\r\n  0%,\r\n  20%,\r\n  50%,\r\n  80%,\r\n  100% {\r\n    transform: translateY(0);\r\n  }\r\n  40% {\r\n    transform: translateY(-15px);\r\n  }\r\n  60% {\r\n    transform: translateY(-10px);\r\n  }\r\n}\r\n";